<template>
  <div
    class="log-item-wrap"
    :style="`${colorStyle(false)}margin-left: ${level - 1 ? 20 : 0}px;`"
  >
    <link-log
      :data="data"
      :style="`${colorStyle(
        true,
        data.children && data.children.length
      )}margin-left: ${level - 1 ? 20 : 0}px;`"
      :visible="visible"
      :desc="desc"
      @visible-change="visibleChange"
      @sort-change="sortChange"
    ></link-log>
    <div v-if="renderer" v-show="visible">
      <template v-if="data.children && data.children.length">
        <log-item
          v-for="item in sortList"
          :key="item.id"
          :level="level + 1"
          :data="item"
        ></log-item>
      </template>
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  components: {
    linkLog: () => import('./linkLog.vue'),
    logItem: () => import('./logItem.vue'),
  },
  props: {
    data: {
      type: Object,
    },
    level: {
      type: Number,
      default: 1,
    },
  },
  data() {
    return {
      renderer: false,
      visible: false,
      desc: false,
      colors: [
        'rgba(144, 223, 144)', //绿
        'rgba(30, 200, 195)', //青
        'rgba(225, 149, 225)', //粉
        'rgba(233, 233, 92)', //黄
        'rgba(246, 188, 83)', // 橙
        '#7f7fe7', //紫
        'rgba(233, 118, 118)', //红
      ],
    }
  },
  created() {},
  mounted() {},
  methods: {
    visibleChange() {
      if (!this.renderer) {
        this.renderer = true
      }
      this.visible = !this.visible
    },
    sortChange() {
      this.desc = !this.desc
    },
  },

  computed: {
    colorStyle() {
      return function (isParent, hasChild) {
        if (isParent) {
          const index = (this.level % 7) - 1
          const color = this.colors[index]
          return hasChild ? `border-left: 3px solid ${color};padding-left: 3px;` : ''
        } else {
          const index = (this.level % 7) - 2
          const color = this.colors[index]
          return color ? `border-left: 3px solid ${color};padding-left: 3px;` : ''
        }
      }
    },
    sortList() {
      return _.cloneDeep(this.data.children).sort((a, b) => {
        return (new Date(a.time) - new Date(b.time)) * (this.desc ? -1 : 1)
      })
    },
  },

  watch: {},
}
</script>
<style lang='less' scoped>
.log-item-wrap {
  white-space: normal !important;
}
</style>